.App {
  text-align: center;
}

.App-header {
  background-color: #222;

  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

gameboard{
  background-image: url(https://n6-img-fp.akamaized.net/free-photo/texture-background_1404-99.jpg?size=338&ext=jpg);
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
}

table{
  width: 360px;
  -ms-flex-item-align: center;
      align-self: center;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}
button.restart{
  background-color:green;
  height: 35px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

button.boardbutton{
  height: 40px;
  width: 40px;
  margin: 2px;
}
table {
  border: 1px solid black;
  border-collapse: collapse;
}


